<template>
  <div class="wrap">
    <div class="head">
      <van-icon @click="go" name="arrow-left" />
      <p>新增地址</p>
      <p></p>
    </div>
    <div class="yi">
      <span>联系人</span>
      <input type="text" placeholder="姓名" v-model="linkMan" />
    </div>
    <div class="yi">
      <span>手机号码</span>
      <input type="text" placeholder="11位手机号" v-model="mobile" />
    </div>
    <div class="yi1" @click="ad">
      <span>选择地区</span>
    </div>
    <div class="xuan">
      <van-popup v-model="show" position="bottom" :style="{ height: '30%' }">
        <van-area :area-list="areaList" @confirm="ok" @cancel="cunc" value="110101" />
      </van-popup>
    </div>

    <div class="yi">
      <span>详细地址</span>
      <input type="text" placeholder="街道门牌信息" v-model="address" />
      <div v-if="ares.length>0">
        <span>{{ ares[0].name }}</span>
        <span>{{ ares[1].name }}</span>
        <span>{{ ares[2].name }}</span>
      </div>
    </div>
    <div class="yi">
      <span>邮政编码</span>
      <input type="text" placeholder="邮政编码" v-model="code" />
    </div>
    <div id="btn">
      <p @click="cunc">保存</p>
      <p>从微信读取</p>
      <p>取消</p>
    </div>
  </div>
</template>

<script>
import areaList from "../area";
import qs from "qs";
export default {
  props: {}, // 组件参数 接收来自父组件的数据
  data() {
    return {
      //模拟数据
      show: false,
      areaList: areaList,
      ares: [],
      linkMan: "",
      mobile: "",
      address: "",
      code: ""
    };
  },
  created() {
    //创建后
  },
  components: {}, // 局部注册的组件
  mounted() {
    //加载后
  },
  watch: {}, // 负责监听
  computed: {}, // 计算属性
  methods: {
    //方法集合
    //确认
    ad() {
      this.show = true;
    },
    //数据
    ok(data) {
      console.log(data);
      this.ares = data;
      this.show = false;
    },
    //取消
    cancel() {
      this.show = false;
    },
    cunc() {
      var data = {
        address: this.address,
        cityId: this.ares[1].code,
        code: this.code,
        linkMan: this.linkMan,
        mobile: this.mobile,
        provinceId: this.ares[0].code,
        token: localStorage.getItem("token")
      };
      var d = qs.stringify(data);
      this.$http
        .post("https://api.it120.cc/small4/user/shipping-address/add", d)
        .then(msg => {
          console.log(msg);
          if (msg.data.msg == "success") {
            this.$router.push("/dizlist");
          }
        });
    },
    go() {
      this.$router.go(-1);
    }
  }
};
</script>

<style scoped lang="less">
.wrap {
  font-size: 0.2rem;
  .head {
    display: flex;
    justify-content: space-between;
    height: 0.7rem;
    align-items: center;
    padding: 0 0.2rem;
    font-size: 0.2rem;
    box-sizing: border-box;
  }
  .yi {
    display: flex;
    margin: 0 0.2rem;
    // box-sizing: border-box;
    height: 0.7rem;
    align-items: center;
    border-bottom: 0.02rem solid #dddddd;
    > span {
      margin-right: 0.2rem;
      color: #000;
    }
    > input {
      border: none;
      outline: none;
    }
  }
  .yi1 {
    display: flex;
    margin: 0 0.2rem;
    // box-sizing: border-box;
    height: 0.7rem;
    align-items: center;
    border-bottom: 0.02rem solid #dddddd;
    > span {
      margin-right: 0.2rem;
      color: #000;
    }
  }
  .xuan {
    width: 100%;
    position: fixed;
    bottom: 0rem;
    right: 0rem;
    .van-popup {
    }
  }

  #btn > p:nth-child(1) {
    background: linear-gradient(to right, #81c125, #1aac19);
  }
  #btn > p:nth-child(2) {
    background: linear-gradient(to right, #4bb0ff, #6149f6);
  }

  #btn {
    padding: 0 0.2rem;
    box-sizing: border-box;
  }
  #btn p {
    width: 100%;
    background: #dddddd;
    text-align: center;
    line-height: 0.56rem;
    color: white;
    margin-top: 0.23rem;
    font-size: 0.22rem;
    letter-spacing: 0.02rem;
    border-radius: 0.06rem;
    height: 0.56rem;
  }
}
</style>



